/**완전판매확인서 */
import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import {Button, Flex, Modal, WhiteSpace} from "antd-mobile";
import 'antd-mobile/dist/antd-mobile.css';
import moment from "moment";
import * as service from 'service/post';
const alert = Modal.alert;

const fledDivStyle3 = {
    display : 'flex',
    height :'30px',
    justifyContent: 'center',
    alignItems: 'center'
}

export default function PerfectSale(props){
    const history = useHistory();
    const [checkBtnConfirm, setCheckBtnConfirm] = useState(false);
    const [successLoad, setSuccessLoad] = useState(false);
    const nowDateDay = moment().format('yyyy년 MM월 DD일');
    const [userData, setUserData] = useState({});
    const alerText = {text: "확인"};

    useEffect(() => {
        console.log(props.location.state);
        try{
            if(!props.location.state.userData)
            {
                alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
            }else{
                setSuccessLoad(true);
                setUserData(props.location.state.userData);
            }
        }catch(error){
            alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
        }
        console.log(userData);
    }, [])

    const btnConfirmClickHandler = e =>
    {
        alert("알림", '위 내용에 동의 하고 서명 하시겠습니까?',[
            {
                text : '아니요',
                onPress : () => {
                    setCheckBtnConfirm(false);
                },
            },
            {
                text : '예',
                onPress : () => {
                    sendAgreeDate(false);
                },
            }
        ]);
    }

    const sendAgreeDate = async() =>{
        let params = {
            url: '/Imo/MobileBigyo/SaveAgreeDate',
            pk : props.location.state.userData.userInfo.pk,
            gubun : 'comp_agree_date',
            imochk: 'ok',
        };
        let result = await service.getPost(params);

        if(result.data.status.ercode !== '0'){
            alert('알림', result.data.status.ermsg, [alerText]);
        }else{
            setCheckBtnConfirm(true);
        }
    }

    const nextButtonClickHandler = e =>{
        history.replace("/perfectSale", {userData : props.location.state.userData});
    }
    return (
        <>  
        {
            successLoad?
            <div>
                <div style={{textAlign : 'center'}}>
                    완전판매확인서
                </div>
                <div style={{textAlign : 'center', border: 'solid thin', backgroundColor : 'aliceblue'}}>
                    보험 가입을 진심으로 감사드립니다.
                </div>
                <WhiteSpace/>
                <div style={{border: 'solid thin'}}>
                    <Flex>
                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            계약자
                        </div>
                        <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                            {userData.userInfo.geyakja_nm}
                        </div>
                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            생년월일
                        </div>
                        <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                            {userData.userInfo.birth_day}
                        </div>             
                    </Flex>
                    <Flex>
                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            피보험자
                        </div>
                        <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                            {userData.userInfo.pibohumja_nm}
                        </div>
                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            월보험료
                        </div>
                        <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                            {userData.userInfo.youngsu_p}
                        </div>
                    </Flex>
                    <Flex>
                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            보험회사
                        </div>
                        <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                            {userData.sangpumListData[0].wonsusa_nm}
                        </div>
                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            보험종목
                        </div>
                        <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                            {userData.userInfo.sangpum_category_nm}
                        </div>             
                    </Flex>
                    <Flex>
                    <div style={{width : '20%', textAlign : 'center', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            보험상품명
                        </div>
                        <div style={{width : '70%', textAlign : 'center'}}>
                            {userData.sangpumListData[0].sangpum_nm}
                        </div>
                    </Flex>
                </div>
                <WhiteSpace/>
                <div>
                    2. 비교설명들은 보험회사 및 보험상품(3개이상의 동종, 유사 상품 목록)
                </div>                
                <WhiteSpace/>
                <div style={{border: 'solid thin'}}>
                    <Flex>
                        <div style={{...fledDivStyle3, width : '10%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            구분
                        </div>
                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            보험회사
                        </div>
                        <div style={{...fledDivStyle3, width : '70%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            보험상품명
                        </div>
                    </Flex>
                    {
                        userData.sangpumListData.map(val => {
                            return (
                                <Flex>
                                    <div style={{...fledDivStyle3, width : '10%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                        {val.no}
                                    </div>
                                    <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                        {val.wonsusa_nm}
                                    </div>
                                    <div style={{...fledDivStyle3, width : '70%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                        {val.sangpum_nm}
                                    </div>
                                </Flex>
                            );
                        })
                    }
                </div>
                <WhiteSpace/>
                <div>
                    3. 보험상품 비교설명 관련 사항
                </div> 
                <WhiteSpace/>
                <div style={{border: 'solid thin'}}>
                본인은 상기 보험상품에 청약하면서 3가지 이상의 동종 또는 유사 상품에 대하여 인카금융서비스 대리점 소속 모집종사자로부터 ①보험금 및 지급사유, ②보험기간
                ③보험료 ④보험금을 지급하지 아니하는 사유, ⑤해지환급금에 관한 사항 ⑥재계약에 관한 사항, ⑦해당 보험상품의 차별화된 특징에 대하여 비교·설명을
                <br/>
                <label style={{color :'red'}}>(들었습니다.)</label>
                </div>
                <WhiteSpace/>
                <div style={{textAlign : 'center'}}>
                    {nowDateDay}
                </div>
                <WhiteSpace/>
                <div>
                    <Flex style={{justifyContent: 'flex-end'}}>
                        <div style={{wordSpacing : '2rem'}}>
                            계약자 {userData.userInfo.geyakja_nm}
                        </div>
                        <div style={{paddingLeft : '3em'}}>
                            <Button type="primary" inline="true" size="small"
                                style={{display : checkBtnConfirm?'none':''}} onClick={btnConfirmClickHandler}>
                                확인
                            </Button>
                            <div style={{display : checkBtnConfirm?'':'none'}}>
                                ({userData.userInfo.geyakja_nm})
                            </div>
                        </div>
                    </Flex>
                </div>
                <WhiteSpace/>
                <div style={{border: 'solid thin'}}>
                    <Flex>
                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            확인자소속
                        </div>
                        <div style={{...fledDivStyle3, width : '35%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                            인카금융서비스(주)
                        </div>
                        <div style={{...fledDivStyle3, width : '15%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                            설계사
                        </div>
                        <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                            {userData.userInfo.sawon_nm}
                        </div>             
                    </Flex>
                </div>
                <WhiteSpace/>
                <div>
                    <Button type="primary" disabled={checkBtnConfirm?false:true } block="true" onClick={nextButtonClickHandler}>다음</Button>
                </div>
            </div>
            :
            <div>잘못된 접근</div>
        }
        </>
    )
};